// src/components/ChatWindow.tsx
"use client";
import React, { useEffect, useState } from "react";
import dayjs from "dayjs";
import { fetchMessages, sendMessage } from "../lib/api";

interface ChatWindowProps {
  wa_id?: string;
  name?: string;
}

export default function ChatWindow({ wa_id, name }: ChatWindowProps) {
  const [messages, setMessages] = useState<any[]>([]);
  const [text, setText] = useState("");

  useEffect(() => {
    if (!wa_id) return;
    fetchMessages(wa_id).then(setMessages).catch(console.error);
  }, [wa_id]);

  const handleSend = async () => {
    if (!text.trim() || !wa_id) return;
    await sendMessage({
      wa_id,
      name,
      from: "me",
      text,
      timestamp: new Date().toISOString(),
      status: "sent",
    });
    setText("");
    fetchMessages(wa_id).then(setMessages);
  };

  return (
    <section className="flex-1 flex flex-col bg-gray-50">
      {/* Header */}
      <div className="h-16 bg-gray-100 flex items-center px-4 border-b border-gray-300">
        <div className="w-10 h-10 rounded-full bg-green-500 flex items-center justify-center text-white font-bold mr-3">
          {name?.[0] || "?"}
        </div>
        <div>
          <div className="text-gray-900 font-medium">{name}</div>
          <div className="text-gray-500 text-xs">last seen recently</div>
        </div>
      </div>

      {/* Messages */}
      <div className="flex-1 overflow-y-auto p-6 space-y-3">
        {messages.map((m, i) => (
          <div key={i} className={m.from === "me" ? "flex justify-end" : "flex justify-start"}>
            <div
              className={`max-w-xs rounded-lg px-3 py-2 text-sm shadow ${
                m.from === "me"
                  ? "bg-green-100 text-gray-900"
                  : "bg-white text-gray-900 border border-gray-200"
              }`}
            >
              {m.text}
              <div className="text-[10px] text-gray-500 text-right mt-1">
                {m.timestamp ? dayjs(m.timestamp).format("HH:mm") : ""}
              </div>
            </div>
          </div>
        ))}
      </div>

      {/* Input */}
      <div className="h-16 flex items-center px-4 bg-gray-100 border-t border-gray-300 space-x-3">
        <input
          value={text}
          onChange={(e) => setText(e.target.value)}
          placeholder="Type a message"
          className="flex-1 px-3 py-2 rounded-full bg-white border border-gray-300 text-sm focus:outline-none"
        />
        <button
          onClick={handleSend}
          className="bg-green-500 text-white px-4 py-2 rounded-full font-medium hover:bg-green-600"
        >
          Send
        </button>
      </div>
    </section>
  );
}
