// src/components/ChatWindow.tsx
"use client";
import React, { useEffect, useState } from "react";
import dayjs from "dayjs";
import { fetchMessages, sendMessage } from "../lib/api";

interface ChatWindowProps {
  wa_id?: string;
  name?: string;
}

export default function ChatWindow({ wa_id, name }: ChatWindowProps) {
  const [messages, setMessages] = useState<any[]>([]);
  const [text, setText] = useState("");

  useEffect(() => {
    if (!wa_id) return;
    fetchMessages(wa_id).then(setMessages).catch(console.error);
  }, [wa_id]);

  const handleSend = async () => {
    if (!text.trim() || !wa_id) return;
    await sendMessage({
      wa_id,
      name,
      from: "me",
      text,
      timestamp: new Date().toISOString(),
      status: "sent",
    });
    setText("");
    fetchMessages(wa_id).then(setMessages);
  };

  return (
    <section className="flex-1 flex flex-col bg-wa-dark">
      {/* Header */}
      <div className="h-16 bg-wa-dark3 flex items-center px-4 border-b border-gray-700">
        <div className="w-10 h-10 rounded-full bg-wa-green flex items-center justify-center text-white font-bold mr-3">
          {name?.[0] || "?"}
        </div>
        <div>
          <div className="text-wa-light font-medium">{name}</div>
          <div className="text-gray-400 text-xs">last seen recently</div>
        </div>
      </div>

      {/* Messages */}
      <div className="flex-1 overflow-y-auto p-4 space-y-2">
        {messages.map((m, i) => (
          <div
            key={i}
            className={m.from === "me" ? "flex justify-end" : "flex justify-start"}
          >
            <div
              className={`max-w-xs rounded-lg px-3 py-2 text-sm ${
                m.from === "me" ? "bg-wa-bubbleOut text-wa-light" : "bg-wa-bubbleIn text-wa-light"
              }`}
            >
              {m.text}
              <div className="text-[10px] text-gray-400 text-right mt-1">
                {m.timestamp ? dayjs(m.timestamp).format("HH:mm") : ""}
              </div>
            </div>
          </div>
        ))}
      </div>

      {/* Input */}
      <div className="h-16 flex items-center px-4 bg-wa-dark3 space-x-3 input-area">
        <input
          value={text}
          onChange={(e) => setText(e.target.value)}
          placeholder="Type a message"
          className="flex-1 px-3 py-2 rounded-lg bg-wa-dark text-wa-light text-sm focus:outline-none"
        />
        <button
          onClick={handleSend}
          className="bg-wa-green text-white px-4 py-2 rounded-lg font-medium hover:bg-emerald-600"
        >
          Send
        </button>
      </div>
    </section>
  );
}
